export default function maps() {
	const mapsel = document.querySelectorAll('.map')

	function initMap(coordLat, coordLng, el) {
		let coordinates = { lat: coordLat, lng: coordLng }

		console.log(coordinates)

		let map = new google.maps.Map(el, {
			center: coordinates,
			disableDefaultUI: true,
		})

		let marker = new google.maps.Marker({
			position: coordinates,
			map: map,
		})
	}

	mapsel.forEach(element => {
		const coordLat = +element.dataset.lat
		const coordLng = +element.dataset.lng

		initMap(coordLat, coordLng, element)
	})
}
